.oleSidebar {
    width: 400px;
    max-height: 500px;
    background-color: #f8f9fa;
    overflow-y: auto;
    transition: all .3s;
}

.oleSidebar-Menu-Title {
    color: #ffff;
    background-color: #009bde;
    font-size: 1.15rem;
}

.oleSidebar .list-group-item {
    border-color: #009bde;
}

@media screen and (max-width : 768px){
    .oleSidebar {
        width: 250px;
    }

    .oleSidebar-Menu-Title {
        font-size: 1rem;
        font-weight: bold;
    }
    
}